@import "../../global.scss";

.portfolio{
     background-color: rgb(134, 131, 131);
     display: flex;
     flex-direction: column;
     align-items: center;

     h1{
         font-size: 50px;
         color: rgb(221, 61, 61);

          @include mobile{
            font-size: 30px;
          }
     }

     ul{
         margin: 10px;
         padding: 0;
         list-style: none;
         display: flex;
         color :rgb(252, 246, 225);

         @include mobile{
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
          }
         
     }

     .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
          }

        .item{
            width: 500px;
            height: 300px;
            border-radius: 20px;
            border: 1px solid rgb(67, 70, 71);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: rgb(49, 48, 48);;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 180px;
                height: 120px;
              }

            h3{
                position: absolute;
                font-size: 20px;
    
            }
    
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }
    
            &:hover {
                background-color: $mainColor;
                img {
                  opacity: 0.2;
                  z-index: 0;
                }
            }
        }

       
     }
 }